import React, { FC, useEffect, useState } from 'react';
import { Scene } from '@antv/l7';
import { GaodeMap } from '@antv/l7-maps';
import AMapLoader from '@amap/amap-jsapi-loader';

import { addPointLayer, addProvinceLayer } from './util';
import { ContextMenu } from '..';
import estateService from '../../service/estate';

const Map: FC = (props) => {
    const [leftTop, setLeftTop] = useState<number[]>([]);
    useEffect(() => {
        const initAMap = async () => {
            const AMap = await AMapLoader.load({
                key: '15cd8a57710d40c9b7c0e3cc120f1200', // WebKey load 
                version: '1.4.15', //  JSAPI  1.4.15
                plugins: [], // 'AMap.Scale'
                // AMapUI: {
                //     //  AMapUI
                //     version: "1.1", // AMapUI  1.1
                //     plugins: [], //  AMapUI ui
                // },
                // Loca: {
                //     //  Loca 
                //     version: "1.3.2", // Loca  1.3.2
                // },
            });
            const map = new AMap.Map('map', {
                viewMode: '3D',
                pitch: 0,
                mapStyle: 'amap://styles/darkblue',
                center: [121.435159, 31.256971],
                zoom: 14.89,
                zooms: [10, 100],
            });
            const scene = new Scene({
                id: 'map',
                map: new GaodeMap({
                    mapInstance: map,
                }),
                logoVisible: false,
            });

            scene.on('loaded', async () => {
                const { data } = await estateService.getAllEstates();
                addPointLayer(scene, data);
                addProvinceLayer(scene);
            });
            scene.on('contextmenu', async (evt) => {
                setLeftTop([evt?.pixel?.x, evt?.pixel?.y]);
            });
            scene.on('click', (evt) => {
                setLeftTop([]);
            });
        };
        initAMap();
        return () => {
            // scene.off()
        };
    }, []);

    return (
        <>
            <div id="map"></div>
            <ContextMenu
                show={!!leftTop?.length}
                left={leftTop[0]}
                top={leftTop[1]}
                menuConfigs={[{ name: '', onClick: () => setLeftTop([]) }]}
            />
        </>
    );
};

export default Map;
